.cart-widget {
    font-family: 'Montserrat', sans-serif;
}

.cart-icon {
    color: #E63946;
    font-size: 1.5em;
}

.cart-icon:hover {
    color: #1D3557;
}

.cart-span {
    font-size: 1.2rem;
}
.navbar {
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-family: 'Montserrat', sans-serif;
    color: #E63946;
    margin: 2em 0;
}

.menu-link {
    padding: 10px;
    text-transform: uppercase;
}

.menu-link:hover {
    color: #1D3557;
}

.active-class {
    color: #1D3557;
}

.navbar-light .navbar-nav .nav-link {
    color: #E63946;
}
.footer {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
}

.social-media {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 0 !important;
}

.sm-icon {
    padding: 10px;
    font-size: 1.8em;
}

a {
    color: #E63946 !important;
}

a:hover {
    color: #1D3557 !important;
}
.container-slider {
    display: flex;
    flex-wrap: nowrap;
    overflow: hidden;
}

.slide {
    min-width: 100%;
    max-height: 500px;
}
.card {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px #E63946 solid;
    border-radius: 15px;
    margin: 0.8em 0.5em;
    padding-bottom: 1.5em;
    text-align: center;
    overflow: hidden;
    font-family: 'Montserrat', sans-serif;
}

.card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.card-img {
    margin-top: 0.7em;
    transition: all 1s linear;
}

.card-img:hover {
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
    opacity: 1;
}

.card-name, .card-desc, .card-price {
    margin: 0.35em 0.35em 0.7em 0.35em;
}

.card-name {
    text-transform: uppercase;
    color: #E63946;
}

.card-price {
    margin-bottom: 1.4em;
}

.card-btn {
    background-color: #E63946;
    color: #F1FAEE !important;
    border: 2px #E63946 solid;
    border-radius: 15px;
    padding: 12px;
    text-transform: uppercase;
    font-weight: bold;
    cursor: pointer;
}

.card-btn:hover {
    color: #E63946 !important;
    background-color: #F1FAEE;
}

/* MEDIA QUERIES */

@media only screen and (min-width: 1024px) {
    .card {
        flex-grow: 0;
        flex-shrink: 1;
        flex-basis: calc(25% - 1em);
    }
}
.container {
    padding: 2em;
}

.container-title {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 2.7em;
    font-weight: 700;
    color: #1D3557;
}

.row {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
}
.loading {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px 20px;
    min-height: 400px;
    font-family: 'Montserrat', sans-serif;
    font-size: 2em;
    color: #1D3557;
    text-transform: uppercase;
}

.loader {
    border: 8px solid #f3f3f3;
    border-top: 8px solid #1D3557;
    border-bottom: 8px solid #1D3557;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    -webkit-animation: spin 2s linear infinite;
            animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

/* MEDIA QUERIES */

@media only screen and (min-width: 1024px) {
    .loading {
        margin: 200px 200px;
    }
}
.button-box {
    display: flex;
    justify-content: center;
    align-items: center;
}

.card-btn:hover {
    color: #E63946;
    background-color: #F1FAEE;
}

.finish-btn, .minus-button, .plus-button, .add-button {
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    text-transform: uppercase;
    background-color: #E63946;
    color: #F1FAEE;
    border: 2px #E63946 solid;
    border-radius: 12px;
    padding: 7px 10px;
    margin: 0.75em;
    cursor: pointer;
}

.finish-btn, .minus-button:hover, .plus-button:hover, .add-button:hover {
    color: #E63946;
    background-color: #F1FAEE;
}

.minus-button, .plus-button {
    font-size: 1.5rem;
}
.city-container {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(12, 3.5vw);
    grid-gap: 15px;
    padding: 2em 7em;
    font-family: 'Montserrat', sans-serif;
}

.city-img {
    grid-column-start: 1;
    grid-column-end: 7;
    grid-row-start: 1;
    grid-row-end: 6;
    width: 100%;
    height: auto;
}

.city-box {
    grid-column-start: 1;
    grid-column-end: 7;
    grid-row-start: 7;
    grid-row-end: 13;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0 0 !important;
    background-color: #eef0f2;
}

.city-name {
    width: 100%;
    height: auto;
    margin: 1em 0;
    padding: 0.3em 0.3em;
    background-color: #E63946;
    opacity: 0.8;
    font-size: 2.5em;
    font-weight: 700;
    text-transform: uppercase;
    color: #F1FAEE;
}

.city-ticket-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.city-price {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2em;
    font-weight: 700;
    margin: 0 1em;
}

.icon-price {
    font-size: 2em;
    font-weight: 400;
    margin: 0 0.25em;
}

.city-desc {
    margin: 1em 0;
    text-align: center;
}

.city-btn {
    background-color: #E63946;
    color: #F1FAEE !important;
    border: 2px #E63946 solid;
    border-radius: 15px;
    padding: 12px;
    text-transform: uppercase;
    font-weight: bold;
    cursor: pointer;
}

.city-btn:hover {
    color: #E63946 !important;
    background-color: #F1FAEE;
}

.back-btn-box {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1.5em 0;
    font-family: 'Montserrat', sans-serif;
}

/* MEDIA QUERIES */

@media only screen and (min-width: 1024px) and (max-width: 1024px) {
    .back-btn-box {
        margin: 5em 0;
    }
}

@media only screen and (min-width: 1024px) {
    .city-box {
        padding: 10px;
    }

    .city-data-box{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(3, 2.5vw);
        grid-gap: 10px;
    }

    .city-ticket-box {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        margin-top: 3em;
    }
}
.about-container {
    font-family: 'Montserrat', sans-serif;
}

.about-title {
    margin-top: 1em;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 1.8rem;
    color: #E63946;
}

.about-text {
    margin-top: 1em;
}

.about-p {
    margin: 1em 0;
}
.cart-screen-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    margin: 0 1em;
}

.cart-screen-container-full {
    display: flex;
    flex-direction: column;
    flex-grow: 0;
    flex-shrink: 1;
    flex-basis: calc(25% - 1em);
    justify-content: center;
    align-items: center;
    text-align: center;
    border: 2px #E63946 solid;
    border-radius: 15px;
    margin: 0.8em 0.5em;
    padding: 1.5em 0.2em;
    overflow: hidden;
}

.cart-screen-container-full:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.cart-screen-box {
    display: flex;
    flex-direction: row;
    flex-grow: 0;
    flex-shrink: 1;
    flex-basis: calc(25% - 1em);
    justify-content: center;
    align-items: center;
    text-align: center;
}

.card-screen-img {
    width: 10%;
    height: auto;
    border-radius: 15px;
}

.cart-screen-title {
    text-transform: uppercase;
}

.card-screen-img, .cart-screen-name, .cart-screen-text, .cart-screen-trash {
    margin: 0.35em 0.7em 0.7em 0.35em;
}

.cart-screen-name {
    max-width: 5em;
    min-width: 5em;
}

.cart-screen-trash {
    font-size: 1.5rem;
    cursor: pointer;
}

.btn-box {
    display: flex;
    justify-content: center;
    align-items: center;
}

.btn-empty {
    margin: 0 0.5em 1.6em 0.5em;
}

/* MEDIA QUERIES */

@media only screen and (min-width: 1024px) {
    .cart-screen-container-full {
        margin: 0.8em 0.5em;
        padding: 1.5em;
    }
    
    .card-screen-img {
        width: 25%;
    }
}
/* --- RESET --- */

* {
    margin: 0;
    padding: 0;
    color: inherit;
    text-decoration: none;
    list-style-type: none;
    box-sizing: border-box;
}

/* --- OTHER --- */

html {
    font-family: 'Montserrat', sans-serif;
}

a {
    text-decoration: none !important;
}
.checkout-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
}

.checkout-title {
    margin-bottom: 0;
    text-align: center;
    text-transform: uppercase;
}

.form-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.input {
    margin: 1em 0;
    padding: 10px 50px;
    border: 0.8px #E63946 solid;
    border-radius: 2px;
}

.form-btn {
    margin: 1.5em 0.5em 1.6em 0.5em;
}
